body {
    background-color: black;
    color: white;
}

.App {
    text-align: center;
    padding: 5%;
}

.App-title {
    font-size: 25px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}


/*Artist Profile Styles*/

.Artist-Profile {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: center;
        justify-content: center;
    margin: 2%;
}

.Artist-Profile-Image {
    width: 150px;
    height: 150px;
    border: 3px solid white;
    -o-object-fit: cover;
       object-fit: cover;
    border-radius: 75px;
}

.Artist-Profile-Information {
    margin-left: 10px;
    text-align: left;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}

.Artist-Name {
    font-size: 25px;
}

.Artist-Followers,
.Artist-Genres {
    font-size: 18px;
}


/*Artist Gallery Styles*/

.Track {
    display: inline-block;
    width: 220px;
    height: 220px;
    margin: 10px;
    cursor: pointer;
    position: relative;
    text-align: left;
}

.Track-Image {
    width: 220px;
    height: 220px;
    -o-object-fit: cover;
       object-fit: cover;
    border: 3px solid white;
    border-radius: 3px;
    position: absolute;
}

.Track-Text {
    background-color: black;
    color: white;
    width: 213px;
    position: absolute;
    margin-left: 3px;
    bottom: 0;
    text-align: center;
    opacity: 0.75;
    padding: 3px;
}

.Track-Play {
    position: absolute;
    width: 220px;
    height: 220px;
}

.Track-Play-Inner {
    position: absolute;
    text-align: center;
    width: 60px;
    height: 60px;
    border-radius: 30px;
    background-color: black;
    color: white;
    padding: 9px;
    margin: 80px 0 0 80px;
    font-size: 25px;
    opacity: 0;
}

.Track-Play-Inner:hover {
    -webkit-animation-name: fade-in;
            animation-name: fade-in;
    -webkit-animation-duration: 0.5s;
            animation-duration: 0.5s;
    opacity: 0.8;
}

@-webkit-keyframes fade-in {
    from {
        opacity: 0;
    }
    to {
        opacity: 0.8;
    }
}

@keyframes fade-in {
    from {
        opacity: 0;
    }
    to {
        opacity: 0.8;
    }
}


/*Footer*/

.Footer {
    font-size: 18px;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
}


